import meme from './meme.png';
import './App.css';
import { createRef } from 'react';
import { Button, Image, Form, FormGroup } from 'react-bootstrap';
import { Container } from './drag/Container';
import { useScreenshot, createFileName } from "use-react-screenshot";






function App() {
  const ref = createRef(null);
  const [image, takeScreenShot]
   = useScreenshot({
    type: "image/jpeg",
    quality: 1.0
  });

  const download = (image, { name = "virgin_vs_chad", extension = "png" } = {}) => {
    const a = document.createElement("a");
    a.href = image;
    a.download = createFileName(extension, name);
    a.click();
  };

  const downloadScreenshot = () => takeScreenShot(ref.current).then(download);

  return (

    <div tabIndex={1} className="App App-header">

      <div style={{"display": "flex", alignItems: "center"}}>
        <h1 className="mt-2">Virgin vs Chad Meme Maker</h1>
        <Button className=" mt-3 m-2"  variant="outline-primary" onClick={downloadScreenshot}>Save</Button>
      </div>
      <div className="mt-2">Double click to create a new box, left click it to edit it, drag it to movie it, and right click it to delete it.</div>

      <div ref={ref} style={{ height: "90vh", width: "100%", backgroundImage: `url(${meme})`, backgroundRepeat: "no-repeat", backgroundSize: "90%", backgroundPosition: "center" }}>
        <Container className="w-100 h-100" hideSourceOnDrag={true}>
        </Container>

      </div>


    </div >
  );
}

export default App;
